import React from 'react';
import Head from 'next/head';
import AppLayout from '../components/AppLayout';
import { Form } from 'antd';

const Signup = () => {
    return (
        <>
        <Head>
            <title>NodeBird</title>
            <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/antd/4.3.5/antd.css" />
        </Head>
        <AppLayout>
            <Form onSubmit={onSubmit}>
                <div>
                    <label hmltFor="user-id">아이디</label>
                    <br />
                    <Input name="user-id" required onChange={onChangeId} />
                </div>
                <div>
                    <label hmltFor="user-nickname">닉네임</label>
                    <br />
                    <Input name="user-nickname" required onChange={onChangeNickname} />
                </div>
                <div>
                    <label hmltFor="user-password">비밀번호</label>
                    <br />
                    <Input name="user-password" required onChange={onChangePassword} />
                </div>
                <div>
                    <label hmltFor="user-password-check">비밀번호 확인</label>
                    <br />
                    <Input name="user-password-check" required onChange={onChangePasswordCheck} />
                </div>
                <div>
                    <Checkbox name="user-term" value={term} onChange={onChangeTerm}>회원가입을 동의합니다.</Checkbox>
                </div>
                <div>
                    <Button type="primary" htmlType="submit">가입하기</Button>
                </div>
            </Form>
        </AppLayout>
        </>
    );
}

export default Signup;